import { useEffect, useState, lazy, Suspense } from 'react';
import { LoadingScreen } from './LoadingScreen';
import { useNavigate } from 'react-router-dom';
import { auth } from './firebase';
import SuspenseLoading from './SuspenseLoading';


const OS1Context = lazy(() => import("./contexts/OS1Context"));
const OS2Context = lazy(() => import("./contexts/OS2Context"));
const Python1Context = lazy(() => import("./contexts/Python1Context"));
const Tutorial = lazy(() => import("./contexts/Tutorial"));
const TutorialPopup = lazy(() => import("./TutorialPopup"));


const Lesson = ({cName, lName}) => {
  const navigate = useNavigate();
  const [loadingScreenVisibility, setLoadingScreenVisibility] = useState(true);
  const [showPopup, setShowPopup] = useState(false);

  useEffect(() => {
    const hasPassedTutorial = localStorage.getItem('hasPassedTutorial') === 'true';
    if (!hasPassedTutorial) {
      setShowPopup(true);
    }
  }, []);

  const handleAccept = () => {
    localStorage.setItem('hasPassedTutorial', 'true');
    setShowPopup(false);
    navigate('/courses/tutorial');
  };

  const handleSkip = () => {
    localStorage.setItem('hasPassedTutorial', 'true');
    setShowPopup(false);
  }

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged(async (user) => {
      if (!user) {
        navigate('/login');
      }
    });

    return () => unsubscribe();
  }, [navigate]);

  useEffect(() => {
    window.auth = auth;
    const externalScript = document.createElement("script");
    externalScript.type = "module";
    externalScript.src = '../../terminal.js'; // ../../ due to react router's current path
    document.body.appendChild(externalScript);

    const handleTerminalReady = () => {
        const container = document.getElementById('screen');

        console.log(window.terminal);

        window.terminal.draw(container);
        window.terminal.showAll();

        const terminalObj = document.getElementById('terminal');

        window.terminal.attachTo(terminalObj);
        window.terminal.startListening();
        
        // Remove the event listener to avoid multiple executions
        window.removeEventListener('terminalReady', handleTerminalReady);

    };

    // Listen for the terminalReady event
    window.addEventListener('terminalReady', handleTerminalReady);

    return () => {
        document.body.removeChild(externalScript);
        // Clean up event listener if needed
        window.removeEventListener('terminalReady', handleTerminalReady);
        // critical!!! page must be reloaded so that terminal script stops
        window.location.reload();
    };
}, []);

  useEffect(() => {
    const startTerminalScreen = () => {
      setLoadingScreenVisibility(false);
      
      // Remove the event listener to avoid multiple executions
      window.removeEventListener('screenStart', startTerminalScreen);

    };

    // Listen for the screenStart event
    window.addEventListener('screenStart', startTerminalScreen);
  
    return () => {
      window.removeEventListener('screenStart', startTerminalScreen);
    };
  }, []);

  useEffect(() => {
    const handleContextMenuClick = (event) => {
      document.getElementById('contextMenu').style.display = 'none';
    };
  
    document.addEventListener('click', handleContextMenuClick);
  
    const newFileLi = document.getElementById('newFile');
    const newFolderLi = document.getElementById('newFolder');
    /* create a new file */
    const handleNewFileClick = (event) => {
      window.terminal.createNewFile(event);
    };
  
    const handleNewFolderClick = (event) => {
      const pDiv2 = document.getElementById('contextMenu');
      console.log('click on folder', pDiv2.path);
      const eDiv2 = document.getElementById(pDiv2.path);
      eDiv2.click();
    };
  
    newFileLi.addEventListener('click', handleNewFileClick);
    newFolderLi.addEventListener('click', handleNewFolderClick);
  
    return () => {
      document.removeEventListener('click', handleContextMenuClick);
      newFileLi.removeEventListener('click', handleNewFileClick);
      newFolderLi.removeEventListener('click', handleNewFolderClick);
    };
  }, []);

  return (
    <>
    <div className='w-auto min-h-[calc(100vh-94px)] max-h-fit bg-gray-100 text-black'>
        <div id="lesson_title" className='text-xl w-full bg-gray-200 text-center font-bold tracking-wider border-b border-gray-400'>
            <span id="courseName">{cName}</span> <span id="lessonName">{lName}</span>
        </div>

      <div id="contextMenu" className="context-menu" style={{ display: "none" }}>
        <ul>
          <li id="newFile">New File</li>
          <li id="newFolder">New Folder</li>
          <li>...</li>
        </ul>
      </div>

        <div className="containerBox" >
          <div className="container__left">
            <Suspense fallback={<SuspenseLoading/>}>
              {cName + lName === 'OSLesson 1' && <OS1Context/>}
              {cName + lName === 'OSLesson 2' && <OS2Context/>}
              {cName + lName === 'PythonLesson 1' && <Python1Context/>}
              {cName + lName === 'InterEdTutorial' && <Tutorial/>}
            </Suspense>

          </div>
          <div className="resizer" id="dragMe"></div>
          <div className="container__right" id="containerRight">
            <div id="loadingScreen" className={loadingScreenVisibility ? "w-full h-full flex bg-gray-200 items-center justify-center relative" : "hidden"}>
              <LoadingScreen/>
              <p className='text-4xl text-white tracking-wide absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2
              border-4 p-4 border-white text-center'>
                Provisioning a terminal...</p>
            </div>
            <div className={!loadingScreenVisibility ? "screen" : "hidden"} id="screen">
            </div>
          </div>
		    </div>
    </div>
    {showPopup && <TutorialPopup onAccept={handleAccept} onSkip={handleSkip}/>}
    </>
  );
};


export { Lesson as default}